/* eslint-disable no-unused-vars */
import React from 'react';
import ReactDOM from 'react-dom';
import ReactMarkdown from 'react-markdown';
import './style.css';
 
const initialState = `# Naslovvv

## Podnaslov
> Podpodnaslov

[FreeCodeCamp](https://www.freecodecamp.com)

\`<h1>This would be a heading</h1>\`

\`\`\`
// this is multi-line code:
for(let i=0; i<arr.length; i++){
    count++;
}\n\`\`\`

This is a

- list item 

This is **bold** and this is _italic_ 

And this is an image: 

![React Logo w/ Text](https://goo.gl/Umyytc)\n`

class Application extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            __html: initialState 
        };
        this.changeText = this.changeText.bind(this);
    }

    changeText(e){
        this.setState({
            __html: e.target.value
        })
    }

    render(){
        return(
            <div id="wrapper">
                <textarea id="editor" onChange={this.changeText} value={this.state.__html} />
                <Preview dangerouslySetInnerHTML={this.state.__html} />
            </div>
        );
    }
}

class Preview extends React.Component{
    // eslint-disable-next-line
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div id="preview">
                <ReactMarkdown source={this.props.dangerouslySetInnerHTML} />
            </div>
        );
    }
}

ReactDOM.render(
    <Application />,
    document.getElementById('root')
);